.message-container {
  display: flex;
  align-items: flex-start;
  margin: 16px 0;
  animation: slideIn 0.3s ease-out;
  
  &.user-message {
    flex-direction: row-reverse;
  }
}

.message-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #48c6ef 0%, #6f86d6 100%);
  color: white;
  flex-shrink: 0;
  margin-left: 12px;
  box-shadow: 0 2px 8px rgba(72, 198, 239, 0.3);
  
  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.message-bubble {
  max-width: 75%;
  padding: 12px 16px;
  border-radius: 12px 12px 4px 12px;
  background: linear-gradient(135deg, #48c6ef 0%, #6f86d6 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(72, 198, 239, 0.2);
  word-wrap: break-word;
  white-space: pre-wrap;
  line-height: 1.5;
}

.message-text {
  font-size: 14px;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
